#modal-gear {
  z-index: 99;
  opacity: 0;
  visibility: hidden;
  justify-content: center;
  align-items: center;
  transition: all .3s ease-in-out;
  display: flex;
  position: fixed;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

#modal-gear.modal-show {
  width: 100vw;
  height: 100vh;
  opacity: 1;
  visibility: visible;
  background-color: #fff5dc80;
  transition: all .25s ease-in-out;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#modal-gear.modal-hide {
  opacity: 0;
  visibility: hidden;
  transition: all .25s ease-in-out;
  transform: translate(-50%, -50%);
}

#modal-gear .modal__content {
  aspect-ratio: 1 / 1;
  height: 100%;
  background: url("modal-blobanimation.76b71a56.svg") center / contain no-repeat fixed;
  outline: 1px solid red;
  position: relative;
}

#modal-gear .modal__close {
  z-index: 100;
  cursor: pointer;
  background: none;
  border: none;
  outline: none;
  position: absolute;
  top: 10vh;
  right: 5vw;
}

#modal-gear .modal__close svg {
  fill: #000f1ebf;
  transition: all .25s ease-in-out;
  transform: scale(1.2);
}

#modal-gear .modal__close:hover svg {
  fill: #001e3c;
  transition: all .25s ease-in-out;
  transform: scale(1.35);
}

#modal-gear .modal__image-container {
  width: auto;
  height: calc(100% - 40px);
  aspect-ratio: 1 / 1;
  outline: 1px solid green;
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: hidden;
  transform: translate(-50%, -50%);
}

